.Start {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  text-align: center;
  background-color: #222;
  height: 100vh;
  padding: 20px;
  color: white;
}

.Start-logo {
  -webkit-animation: Start-logo-spin infinite 20s linear;
          animation: Start-logo-spin infinite 20s linear;
  height: 80px;
}

.Start-button {
  text-decoration: none;
  margin: 1em;
}

.Start-title {
  font-size: 2em;
}

@-webkit-keyframes Start-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes Start-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


